@import "https://fonts.googleapis.com/css2?family=Epilogue:ital,wght@0,100..900;1,100..900&display=swap";
@import "https://cdn.jsdelivr.net/npm/swiper@11/swiper-bundle.min.css";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

a {
  color: #fff;
  font-weight: 400;
  text-decoration: none;
}

header {
  height: 100%;
  width: 100%;
  color: #fff;
  background-color: #141414;
  justify-content: center;
  padding-bottom: 50px;
  font-family: Epilogue;
  display: grid;
}

.header-sec {
  height: 75px;
  width: 80%;
  justify-content: center;
  align-items: center;
  column-gap: 1%;
  margin: auto 132px;
  display: flex;
}

.logo {
  justify-content: start;
  align-items: center;
  font-size: 15px;
  display: flex;
}

.logo img {
  margin-right: 5px;
}

nav {
  flex-grow: 1;
  align-items: center;
  gap: 2em;
  margin-left: 50px;
  font-size: 9px;
  display: flex;
}

.contact-no {
  height: 48px;
  width: 159px;
  text-align: center;
  background-color: #1e2827;
  border-radius: 8px;
  align-content: center;
  font-size: 9px;
}

.hero-sec {
  height: auto;
  width: 80%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: auto 132px;
  display: flex;
}

.hero-sec h1 {
  height: auto;
  width: 70%;
  letter-spacing: -4px;
  font-size: 60px;
  line-height: 62px;
}

.hero-sec p {
  height: auto;
  width: 25%;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
}

.swiper {
  height: 320px;
  width: 436px;
  margin-bottom: 10px;
  margin-left: 0;
}

.swiper-slide {
  height: 255px;
  width: 436px;
  background-color: #1e2827;
  justify-content: center;
  display: flex;
}

.watch-slide-img {
  height: 233px;
  width: 348px;
}

.swiper-button-next {
  width: 40px;
  height: 40px;
  --swiper-navigation-size: 20px;
  --swiper-navigation-top-offset: 90%;
  --swiper-navigation-sides-offset: 80%;
  --swiper-navigation-color: white;
  border: 2px solid #fff;
  border-radius: 8px;
}

.swiper-button-prev {
  width: 40px;
  height: 40px;
  --swiper-navigation-size: 20px;
  --swiper-navigation-top-offset: 90%;
  --swiper-navigation-sides-offset: 0;
  --swiper-navigation-color: white;
  border: 2px solid #fff;
  border-radius: 8px;
}

.swiper-pagination {
  width: fit-content;
  letter-spacing: -2px;
  bottom: 5%;
  left: 83%;
}

.highlight-img {
  height: 394px;
  width: 60%;
}

@media screen and (max-width: 1024px) and (min-width: 769px) {
  header {
    flex-direction: column;
    row-gap: 0;
    display: flex;
  }

  .header-sec {
    width: 95%;
    margin: auto 32px;
  }

  .hero-sec {
    width: 95%;
    flex-direction: row-reverse;
    justify-content: left;
    margin: auto 32px;
  }

  .hero-sec h1 {
    height: auto;
    width: 653px;
    margin-right: 105px;
  }

  .hero-sec p {
    width: 248px;
  }

  .swiper {
    width: 436px;
  }

  .swiper-button-next {
    --swiper-navigation-sides-offset: 75%;
  }

  .swiper-pagination {
    left: 78%;
  }

  .highlight-img {
    width: 100%;
  }
}

.nav-collapse {
  display: none;
}

@media screen and (max-width: 768px) {
  header {
    width: fit-content;
  }

  .header-sec {
    width: 95%;
    grid-template-columns: repeat(2, 50%);
    display: grid;
  }

  .logo {
    font-size: 14px;
    font-weight: 300;
  }

  .header-sec, .hero-sec {
    margin: 0 auto;
  }

  nav, .contact-no {
    display: none;
  }

  .nav-collapse {
    height: 24px;
    color: #fff;
    background: none;
    border: none;
    grid-template-rows: 1px 2px 1px;
    justify-content: left;
    justify-self: end;
    align-items: center;
    gap: 5px;
    font-weight: bold;
    display: grid;
  }

  .nav-collapse div {
    height: 2px;
    width: 20px;
    background-color: #fff;
    border-radius: 8px;
  }

  .nav-collapse div:first-of-type {
    width: 12px;
  }

  .nav-collapse div:last-of-type {
    width: 14px;
  }

  .mobile-menu {
    height: 100svh;
    width: 100dvw;
    z-index: 2;
    background-color: #1e2827;
    margin: 0;
    animation: menu-open .3s;
    position: fixed;
    top: 0;
    left: 0;
  }

  .menu-head {
    grid-template-columns: 50% 50%;
    margin: 20px;
    display: grid;
  }

  .menu-head span {
    height: 12px;
    width: 12px;
    justify-self: end;
    font-weight: 300;
  }

  .nav-mobile {
    height: fit-content;
    width: 100px;
    text-align: center;
    grid-template-rows: repeat(3, 10px);
    justify-content: center;
    align-items: center;
    margin: 50% auto;
    font-size: 14px;
    display: grid;
  }

  .hero-sec {
    width: 95%;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: 1fr;
    display: grid;
  }

  .hero-sec h1 {
    height: auto;
    width: 90%;
    letter-spacing: -2px;
    font-size: 38px;
    line-height: 40px;
  }

  .hero-sec p {
    height: auto;
    width: 70%;
    max-width: fit-content;
    grid-row: 3;
    justify-self: end;
  }

  .swiper {
    width: 80%;
    height: 260px;
  }

  .swiper-slide, .watch-slide-img {
    height: 200px;
  }

  .swiper-button-next {
    width: 40px;
    height: 40px;
    --swiper-navigation-size: 20px;
    --swiper-navigation-top-offset: 85%;
    --swiper-navigation-sides-offset: 90%;
    --swiper-navigation-color: white;
    border: 2px solid #fff;
    border-radius: 8px;
    left: 3rem;
  }

  .swiper-button-prev {
    --swiper-navigation-top-offset: 85%;
  }

  .swiper-pagination {
    width: fit-content;
    letter-spacing: -2px;
    left: unset;
    bottom: 5%;
    right: 0;
  }

  .highlight-img {
    width: 100%;
  }
}

@keyframes menu-open {
  0% {
    width: 0;
    right: 0;
  }

  25% {
    right: 20%;
  }

  100% {
    right: 100%;
  }
}

.menuClose {
  animation: menu-close .3s;
}

@keyframes menu-close {
  0% {
    left: 0;
  }

  75% {
    left: 500px;
  }
}

